function escapeHtmlAttributeValue(s, preserveCR) {
    preserveCR = preserveCR ? '&#13;' : '\n';
    return ('' + s) 
        .replace(/&/g, '&amp;') 
        .replace(/'/g, '&apos;') 
        .replace(/"/g, '&quot;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/\r\n/g, preserveCR) 
        .replace(/[\r\n]/g, preserveCR)
}

module.exports = escapeHtmlAttributeValue
